.popup__container {
  position: relative;
  width: 430px;
  background: white;
  color: black;
  border-radius: 10px;
  padding: 34px 36px 36px;
  margin: 0 auto;
  box-sizing: border-box;
}

@media screen and (max-width: 425px) {
  .popup__container {
    width: 282px;
    padding: 0;
  }
}